"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";

interface PredictionResult {
  status: string;
  prediction: "fraud" | "not fraud";
  confidence: number;
}

export default function FraudDemo() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [result, setResult] = useState<PredictionResult | null>(null);
  const [selectedModel, setSelectedModel] = useState<string>("");
  const [formData, setFormData] = useState({
    Time: "",
    V1: "",
    V2: "",
    V3: "",
    V4: "",
    V5: "",
    V6: "",
    V7: "",
    V8: "",
    V9: "",
    V10: "",
    V11: "",
    V12: "",
    V13: "",
    V14: "",
    V15: "",
    V16: "",
    V17: "",
    V18: "",
    V19: "",
    V20: "",
    V21: "",
    V22: "",
    V23: "",
    V24: "",
    V25: "",
    V26: "",
    V27: "",
    V28: "",
    Amount: "",
  });

  const handleModelChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedModel(e.target.value);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!selectedModel) {
      setError("Please select a model first");
      return;
    }

    setLoading(true);
    setError(null);
    setResult(null);

    try {
      const response = await fetch("/api/predict", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-model-name": selectedModel,
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        throw new Error("Failed to get prediction");
      }

      const data = await response.json();
      setResult(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred");
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  return (
      <div className="container mx-auto px-4 py-8 space-y-8">
      <div className="max-w-4xl mx-auto">
        {/* Model Selection Section */}
        <Card>
          <CardHeader>
            <CardTitle>Select Model</CardTitle>
            <CardDescription>
              Choose a model for fraud detection
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="grid w-full max-w-sm items-center gap-1.5">
                <Label htmlFor="model">Model</Label>
                <select
                  id="model"
                  value={selectedModel}
                  onChange={handleModelChange}
                  className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                >
                  <option value="">Select a model...</option>
                  <option value="svm">Support Vector Machine</option>
                  <option value="random_forest">Random Forest</option>
                  <option value="rf_and_svm">Random Forest + SVM Ensemble</option>
                </select>
              </div>
              {selectedModel && (
                <Alert>
                  <AlertTitle>Model Selected</AlertTitle>
                  <AlertDescription>
                    Using {selectedModel.replace(/_/g, ' ')} model for predictions.
                  </AlertDescription>
                </Alert>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Hero Section */}
        <div className="text-center">
          <h1 className="text-4xl font-bold mb-4">Credit Card Fraud Detection Demo</h1>
          <p className="text-gray-600 mb-4">
            Test our fraud detection model using transaction data.
            Based on the{" "}
            <a
              href="https://www.kaggle.com/datasets/mlg-ulb/creditcardfraud"
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-600 hover:underline"
            >
              Credit Card Fraud Detection dataset
            </a>{" "}
            from Kaggle.
          </p>
        </div>

        {/* Transaction Analysis Card */}
        <Card>
          <CardHeader>
            <CardTitle>Transaction Analysis</CardTitle>
            <CardDescription>
              Enter transaction details below to check for potential fraud
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="Time">Time</Label>
                  <Input
                    id="Time"
                    name="Time"
                    type="number"
                    required
                    value={formData.Time}
                    onChange={handleInputChange}
                    placeholder="Enter time in seconds"
                  />
                </div>
                {Array.from({ length: 28 }, (_, i) => (
                  <div key={`V${i + 1}`} className="space-y-2">
                    <Label htmlFor={`V${i + 1}`}>{`V${i + 1}`}</Label>
                    <Input
                      id={`V${i + 1}`}
                      name={`V${i + 1}`}
                      type="number"
                      step="0.000001"
                      required
                      value={formData[`V${i + 1}` as keyof typeof formData]}
                      onChange={handleInputChange}
                      placeholder={`Enter V${i + 1} value`}
                    />
                  </div>
                ))}
                <div className="space-y-2">
                  <Label htmlFor="Amount">Amount</Label>
                  <Input
                    id="Amount"
                    name="Amount"
                    type="number"
                    step="0.01"
                    required
                    value={formData.Amount}
                    onChange={handleInputChange}
                    placeholder="Enter transaction amount"
                  />
                </div>
              </div>

              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? "Analyzing..." : "Analyze Transaction"}
              </Button>
            </form>

            {error && (
              <Alert variant="destructive" className="mt-4">
                <AlertTitle>Error</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            {result && (
              <Card className="mt-6">
                <CardHeader>
                  <CardTitle>Analysis Result</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <div className="text-lg font-semibold">
                      Prediction:{" "}
                      <span
                        className={
                          result.prediction === "fraud"
                            ? "text-red-600"
                            : "text-green-600"
                        }
                      >
                        {result.prediction === "fraud"
                          ? "Potential Fraud Detected"
                          : "Transaction Appears Safe"}
                      </span>
                    </div>
                    <div>
                      Confidence Score: {(result.confidence * 100).toFixed(2)}%
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
